html, body, .site {
  min-height: 100vh;
}

.site {
  display: flex;
  flex-direction: column;
}

.site-content {
  flex-grow: 1;
}

.Grid {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -webkit-flex-flow: row wrap;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  position: relative;
  margin: 0 auto;
}

.centertext {
  text-align: center;
}

.Grid-cell {
  margin-bottom: 1.875rem;
  flex: 1;
}

@media (max-width: 767.98px) {
  .Grid-cell {
    margin-bottom: 0px;
  }
}

.Grid--gutters {
  margin-left: -1em;
}

.Grid--gutters .Grid-cell {
  padding-left: 1em;
}

.Grid--gutters .Grid--nested .Grid-cell:first-of-type .dev {
  margin-right: 1em;
}

.Grid--right {
  justify-content: flex-end;
}

.Grid--center {
  justify-content: center;
}

.Grid--top {
  align-items: flex-start;
}

.Grid--bottom {
  align-items: flex-end;
}

.Grid--center {
  align-items: center;
}

.Grid-cell--top {
  align-self: flex-start;
}

.Grid-cell--bottom {
  align-self: flex-end;
}

.Grid-cell--center {
  align-self: center;
}

.content-align {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-align-item {
  max-width: 50%;
}

.content-align-item--top {
  align-self: flex-start;
}

.content-align-item--bottom {
  align-self: flex-end;
}

.Grid--cols-2 > .Grid-cell {
  flex: 0 0 100%;
}

.Grid--cols-3 > .Grid-cell {
  flex: 0 0 100%;
}

.Grid--cols-4 > .Grid-cell {
  flex: 0 0 100%;
}

.Grid--cols-6 > .Grid-cell {
  flex: 0 0 calc(50% - 1em);
}

.Grid--cols-12 > .Grid-cell {
  flex: 0 0 calc(33.3333% - 1em);
}

.Grid--layout .aside, .Grid--layout .main {
  flex: 1 100%;
}

.Grid--1of2 > .Grid-cell,
.Grid--1of4 > .Grid-cell:first-of-type,
.Grid--1of3 > .Grid-cell:first-of-type {
  flex: 0 0 100%;
}

.Grid--1of6 > .Grid-cell:first-of-type {
  flex: 0 0 50%;
}

.Grid--fit > .Grid-cell {
  flex: 1;
}

.Grid--full > .Grid-cell {
  flex: 0 0 100%;
}

@media (min-width: 30em) {
  .Grid--cols-4 > .Grid-cell {
    flex: 0 0 calc(50% - 1em);
  }
  .Grid--cols-6 > .Grid-cell {
    flex: 0 0 calc(33.3333% - 1em);
  }
  .Grid--cols-12 > .Grid-cell {
    flex: 0 0 calc(16.6666% - 1em);
  }
  .Grid--layout .aside {
    flex: 1 calc(25% - 1em);
  }
  .Grid--1of2 > .Grid-cell {
    flex: 0 0 50%;
  }
  .Grid--1of6 > .Grid-cell:first-of-type {
    flex: 0 0 30%;
  }
  .Grid--1of4 > .Grid-cell:first-of-type {
    flex: 0 0 50%;
  }
  .Grid--1of3 > .Grid-cell:first-of-type {
    flex: 0 0 100%;
  }
}

@media (min-width: 48em) {
  .Grid--cols-2 > .Grid-cell,
  .Grid--cols-3 > .Grid-cell,
  .Grid--cols-4 > .Grid-cell,
  .Grid--cols-6 > .Grid-cell,
  .Grid--cols-12 > .Grid-cell {
    flex: 1;
  }
  .Grid--layout .main {
    flex: 2;
  }
  .Grid--layout .aside {
    flex: 1;
  }
  .Grid--layout .aside-1 {
    order: 1;
  }
  .Grid--layout .main {
    order: 2;
  }
  .Grid--layout .aside-2 {
    order: 3;
  }
  .Grid--1of2 > .Grid-cell {
    flex: 0 0 50%;
  }
  .Grid--1of6 > .Grid-cell:first-of-type {
    flex: 0 0 16.6666%;
  }
  .Grid--1of4 > .Grid-cell:first-of-type {
    flex: 0 0 25%;
  }
  .Grid--1of3 > .Grid-cell:first-of-type {
    flex: 0 0 30%;
  }
  .Grid--gutters.Grid--nested .Grid-cell:first-of-type .dev {
    margin-right: 0;
  }
}

