body {
  color: #1a1a1a;
  font-family: "Gotham", "Arial", "Helvetica", sans-serif;
  font-size: 1.125rem;
  font-weight: normal;
  line-height: 1.75rem;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6 .h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: "Noto Serif", serif;
  line-height: 1.25em;
  color: #084C61;
}

h1,
.h1 {
  font-size: 3.5rem;
  font-weight: 700;
}

@media (max-width: 767.98px) {
  h1,
  .h1 {
    font-size: 2.25rem;
  }
}

h2,
.h2 {
  font-size: 2.75rem;
  font-weight: 500;
  letter-spacing: -0.025rem;
}

@media (max-width: 767.98px) {
  h2,
  .h2 {
    font-size: 1.875rem;
  }
}

h3,
.h3 {
  font-size: 2.1875rem;
  font-weight: 400;
  letter-spacing: -0.025rem;
}

@media (max-width: 767.98px) {
  h3,
  .h3 {
    font-size: 1.625rem;
  }
}

h4,
.h4 {
  font-size: 1.75rem;
  font-weight: 500;
}

@media (max-width: 767.98px) {
  h4,
  .h4 {
    font-size: 1.375rem;
  }
}

h5,
.h5 {
  font-size: 1.375rem;
  font-weight: 500;
}

@media (max-width: 767.98px) {
  h5,
  .h5 {
    font-size: 1.125rem;
  }
}

p {
  font-family: "Gotham", "Arial", "Helvetica", sans-serif;
  font-weight: 400;
  line-height: 1.5em;
  font-size: 18px;
  color: #1a1a1a;
}

p.txt-small {
  font-size: 0.875rem;
}

@media (max-width: 767.98px) {
  p {
    font-size: 16px;
  }
}

p a {
  text-decoration: underline;
}

a {
  color: #084C61;
  font-weight: 500;
}

a:hover {
  color: #053b4c;
}

strong {
  font-weight: 700;
  color: #084C61;
}

ul li {
  line-height: 1.2em;
  margin-bottom: 20px;
}

@media (max-width: 767.98px) {
  ul li {
    font-size: 16px;
  }
}

@media (min-width: 576px) {
  ul.columns-md-2 {
    columns: 2;
  }
}

@media (min-width: 576px) {
  ul.columns-md-3 {
    columns: 3;
  }
}

@media (min-width: 576px) {
  ul.columns-md-4 {
    columns: 4;
  }
}

