section.hero-section .announcement-banner {
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

section.hero-section .announcement-banner:before {
  content: "";
  position: absolute;
  left: -1200px;
  width: 100%;
  min-height: 450px;
  background: rgba(23, 126, 137, 0.75);
  -webkit-animation: slide 1s forwards;
  -webkit-animation-delay: 1s;
  animation: slide 1s forwards;
  animation-delay: 1s;
  transform: skewX(165deg);
}

@-webkit-keyframes slide {
  100% {
    left: 110px;
  }
}

@keyframes slide {
  100% {
    left: 110px;
  }
}

section.hero-section .announcement-banner .announcement {
  background-color: #762057;
  color: #FFFFFF;
  font-size: 25px;
  line-height: 35px;
  max-width: 350px;
  padding: 15px 30px;
  /*
      max-width: 250px;
      max-height: 100px;
	  */
  -webkit-animation: fillright 1s forwards;
  -webkit-animation-delay: 2s;
  animation: fillright 1s forwards;
  animation-delay: 2s;
  /*
      position: absolute;
      left: -600;
	  */
  transform: scaleX(0);
  transform-origin: left;
}

@-webkit-keyframes fillright {
  100% {
    transform: scaleX(1);
    transform-origin: left;
  }
}

@keyframes fillright {
  100% {
    transform: scaleX(1);
    transform-origin: left;
  }
}

section.hero-section .announcement-banner .announcement span {
  font-family: "Noto Serif", serif;
  opacity: 0;
  -webkit-animation: fadeIn 0.5s forwards;
  -webkit-animation-delay: 3.5s;
  animation: fadeIn 0.5s forwards;
  animation-delay: 3.5s;
}

@-webkit-keyframes fadeIn {
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  100% {
    opacity: 1;
  }
}

section.hero-section .announcement-banner .announcement img {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  -webkit-animation: scaleUp 0.5s forwards;
  -webkit-animation-delay: 4s;
  animation: scaleUp 0.5s forwards;
  animation-delay: 4s;
  transform: scale(0);
}

@-webkit-keyframes scaleUp {
  100% {
    transform: scale(1);
  }
}

@keyframes scaleUp {
  100% {
    transform: scale(1);
  }
}

